@import '../listas/listas.scss';

.navContainer{
    position: fixed;
    top: 0;
    margin: auto;
    z-index: 1;
    background-color: nth($colores, 2);
    height: 80px;
    width: 100%;
    display: flex;
    font-size: 24px;
    font-weight: 500;
    a{
        color: #ffffff;
    }
    >div:nth-child(1){
        width: 20%;
    }
    >div:nth-child(2){
        width: 60%;
        display: flex;
        align-items: center;
        justify-content: center;
        >div:nth-child(2){
            >a{
                padding: 0px 50px 0px 50px;
                >svg{
                    width: 50px;
                    height: 50px;
                    color: #ffffff;
                }
            }
        }
    }
    >div:nth-child(3){
        width: 20%;
        display: flex;
        >div:nth-child(1){
            width: 20%;
        }
        >div:nth-child(2){
            width: 60%;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            >a{
                display: flex;
                align-items: center;
            }
        }
        >div:nth-child(3){
            width: 20%;
            display: flex;
        }
    }
}